import "./App.css";
import "antd/dist/antd.css";
import { Table } from "antd";

function App() {
  const data = [
    {
      name: "Wheat Bread",
      price: 40,
      quantity: "250gm",
      NoofItems: "2",
      key: "1",
    },
    {
      name: "Amul Butter",
      price: 48,
      quantity: "100g",
      NoofItems: "1",
      key: "2",
    },
    {
      name: "Dark Fantasy",
      price: 98,
      quantity: "253gm",
      NoofItems: "3",
      key: "3",
    },
    {
      name: "Whole wheat atta",
      price: 450,
      quantity: "5 kg",
      NoofItems: "2",
      key: "4",
    },
    {
      name: "Alpenliebe",
      price: 50,
      quantity: "174gm",
      NoofItems: "60",
      key: "5",
    },
  ];

  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      key: "key",
    },
    {
      title: "Price",
      dataIndex: "price",
      key: "key",
      sorter: (a, b) => a.price - b.price,
    },
    {
      title: "Quantity",
      dataIndex: "quantity",
      key: "key",
    },
    {
      title: "No of items",
      dataIndex: "NoofItems",
      key: "key",
    },
  ];

  return (
    <div className="App">
      <header className="App-header">
          <div className="tb">
            <h1 style={{ color: "#275E45", marginRight:"3px" }}> Shopping List</h1>
            <Table
              className="table table-striped table-hover table-responsive"
              dataSource={data}
              columns={columns}
            ></Table>
          </div>
      </header>
    </div>
  );
}

export default App;
